body {-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;}
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, button, input, textarea, th, td { margin:0; padding:0; }
h1, h2, h3, h4, h5, h6{ font-size:100%; }
address, cite, dfn, em, var { font-style:normal; }
small{ font-size:12px; }
ul, ol { list-style:none; }
a { text-decoration:none; color: #ffffff;}
a:hover { text-decoration:none; font-weight: bold; }
sup { vertical-align:text-top; }
sub{ vertical-align:text-bottom; }
fieldset, img { border:0; }
input { border:0; outline:none; }
table { border-collapse:collapse; border-spacing:0; }
@charset "UTF-8";
/**
* Global Settings
*/
/* Box Settings */
.box {
  position: relative;
  width: 785px;
  margin: 0 auto;
  border-top: 2px solid #DB021B;
  box-shadow: -1px 10px 15px 0 rgba(0, 0, 0, 0.18);
}
.box .box-header {
    width: 253px;
    height: 70px;
    background: #DB021B;
}
.box .box-header::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 253px;
      border-top: 70px solid #DB021B;
      border-left: 70px solid transparent;
      transform: rotate(-90deg);
}
.box .box-header .box-header-titles {
      color: #ffffff;
      font-size: 23px;
      margin-left: 40px;
}
.box .box-header .box-header-titles::before {
        content: '';
        display: block;
        position: absolute;
        top: 17px;
        width: 3px;
        height: 37px;
        background: #ffffff;
}
.box .box-header .box-header-titles .english {
        margin-left: 17px;
}
.box .box-header .box-header-titles .english::after {
          content: '';
          display: block;
          width: 127px;
          height: 1px;
          background: #ffffff;
}
.box .box-header .box-header-titles .chinese {
        margin-left: 17px;
}
.box .box-content {
    padding: 53px;
}
.box .box-content div {
      margin-bottom: 15px;
}
.box .box-content p {
      line-height: 25px;
      letter-spacing: 1px;
}
.box .box-content p.box-content-title {
        margin-bottom: 5px;
        font-size: 15px;
        font-weight: bold;
        letter-spacing: 2px;
}
.box.\--noshadow {
    box-shadow: none;
}

/* Header Settings */
.header {
  width: 100%;
  height: 1920px;
  background-position: top;
  background-size: cover;
  transition: height 1s;
}
.header #canvas {
    position: absolute;
}
.header .header-titles {
    position: absolute;
    top: 133px;
    left: 0;
    right: 0;
    color: #ffffff;
    text-shadow: 2px 3px 8px #202629;
    text-align: center;
    transition: opacity .6s;
}
.header .header-titles .header-titles-main {
      font-size: 53px;
      font-weight: bold;
      letter-spacing: 2px;
}
.header .header-titles .header-titles-sub {
      font-size: 20px;
      letter-spacing: 1px;
}
.header .header-titles .header-titles-time {
      font-size: 16px;
      font-weight: bold;
      margin-top: 47px;
}

/* Navbar Settings */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
}
.navbar .navbar-inner {
    width: 800px;
    margin: 0 auto;
}
.navbar .navbar-inner .navbar-inner-logo img {
      width: 128px;
      height: 63px;
      background: url("/assets/img/logo.png");
}
.navbar .navbar-inner .navbar-inner-list {
      float: right;
      line-height: 60px;
}
.navbar .navbar-inner .navbar-inner-list .navbar-inner-list-item {
        min-width: 70px;
        position: relative;
        float: right;
        color: #ffffff;
        text-align: center;
        letter-spacing: 1px;
        margin-left: 27px;
}
.navbar .navbar-inner .navbar-inner-list .navbar-inner-list-item.border {
          border: 1px solid #ffffff;
          line-height: 25px;
          margin: 16px 0 20px 20px;
}
.navbar .navbar-inner .navbar-inner-list .navbar-inner-list-item.selected.border::after {
          content: '';
          display: block;
          position: absolute;
          left: 0;
          right: 0;
          bottom: 3px;
          width: 80%;
          height: 1px;
          margin: auto;
          background: #ffffff;
}
.navbar .navbar-inner .navbar-inner-list .navbar-inner-list-item.selected::after {
          content: '';
          display: block;
          position: absolute;
          bottom: 18px;
          width: 100%;
          height: 1px;
          background: #ffffff;
}

/* Description Settings */
.description {
  position: relative;
  height: 1500px;
  margin: 0 auto;
  background: #ffffff;
  /* links */
}
.description #des {
    position: absolute;
    top: 0px;
}
.description #qua {
    position: absolute;
    top: 500px;
}
.description .description-des {
    position: absolute;
    top: 67px;
    left: 0;
    right: 0;
    margin: auto;
    height: 421px;
}
.description .description-qua {
    position: absolute;
    top: 567px;
    left: 0;
    right: 0;
    margin: auto;
}

/* Schedule Settings */
.schedule {
  position: relative;
  height: 633px;
  background-position: center;
  background-size: cover;
  /* links */
}
.schedule #sch {
    position: absolute;
    top: -3px;
}
.schedule .schedule-title {
    position: absolute;
    top: 77px;
    left: 0;
    right: 0;
    margin: auto;
    width: 783px;
    height: 66px;
    background: #DB021B;
    color: #ffffff;
    font-size: 23px;
    line-height: 66px;
    text-align: center;
}
.schedule .schedule-table {
    position: absolute;
    top: 187px;
    left: 0;
    right: 0;
    margin: auto;
    width: 783px;
    height: 267px;
    color: #ffffff;
}
.schedule .schedule-table th {
      width: 87px;
      height: 67px;
}
.schedule .schedule-table th:nth-child(even) {
        background: #DB021B;
}
.schedule .schedule-table th:nth-child(odd) {
        background: #FF0320;
}
.schedule .schedule-table td {
      color: #000000;
      font-size: 12px;
      text-align: center;
}
.schedule .schedule-table td span {
        display: block;
        transform: scale(0.8);
}
.schedule .schedule-table td:nth-child(1) {
        font-weight: bold;
        color: #ffffff !important;
        background: #FFA4A4 !important;
}
.schedule .schedule-table td:nth-child(even) {
        background: #E8E8E8;
}
.schedule .schedule-table td:nth-child(odd) {
        background: #FFFFFF;
}

